.main-block {
  padding-top: 197px;
  background-image: url(../images/backPicOne.jpg);
  background-size: cover;
  height: 760px;
  &__btn {
    margin-top: 40px;
  }
  &__text {
    font-family: FedraSansAltProMedTF;
    margin-bottom: 111px;
    &_white {
      color: #fff;
    }
    &_big {
      letter-spacing: -0.8px;
      line-height: 50px;
      margin-top: 10px;
    }
    &_small {
      margin-top: 19px;
      opacity: 0.5;
      letter-spacing: 0.5px;
      font-size: 11px;
    }
  }
  .package-white {
    fill: #fff;
    font-size: 39px;
  }
}