.container_tel {
  width: 503px;
  margin: 0 auto;
}
.tel {
  margin: 115px 0 38px;
  &__link {
    display: block;
  }
  &_title {
    font-family: FedraSansAltProBookTF;
    display: block;
    font-size: 13px;
    color: @darkGrey;
    letter-spacing: -0.09px;
  }
  &_num {
    margin-top: 15px;
    font-family: FedraSansAltProMedTF;
    display: block;
    font-size: 24px;
    color: @dark;
    letter-spacing: 0;
  }
}