.price {
  margin-top: 46px;
  position: relative;
  background: transparent;
  &:before {
    content: '';
    position: absolute;
    background-image: -webkit-linear-gradient(#FFFFFF 0%, #F4F4F4 100%);
    background-image: -o-linear-gradient(#FFFFFF 0%, #F4F4F4 100%);
    background-image: linear-gradient(#FFFFFF 0%, #F4F4F4 100%);
    width: 100%;
    height: 300px;
    bottom: -109px;
    left: 0;
    z-index: 1;
  }
  &__title {
    line-height: 71px;
  }
  &__list {
    margin-top: 71px;
    &__item {
      position: relative;
      background: #FFFFFF;
      box-shadow: 0 0 8px 0 rgba(0,0,0,0.10), 0 6px 8px 0 rgba(0,0,0,0.06), 0 2px 13px 0 rgba(0,0,0,0.05), 0 2px 4px 0 rgba(0,0,0,0.10);
      border-radius: 1px;
      float: left;
      &_default {
        padding-top: 46px;
        width: 310px;
        z-index: 2;
        .price {
          &__cost {
            margin-top: 91px;
            color: @dark;
            font-size: 80px;
            letter-spacing: -0.8px;
            line-height: 64px;
            &:not(.price__cost_xl) {
              margin-left: 56px;
            }
            &__name {
              margin-right: 35px;
              &_up {
                border-color: @dark;
              }
            }
          }
          &__profit {
            margin-top: 21px;
          }
          &__descript {
            margin-top: 60px;
          }
          &__buy {
            margin-top: 98px;
          }
        }
      }
      &_special {
        padding-top: 70px;
        width: 350px;
        margin-top: -30px;
        z-index: 3;
        position: relative;
        .price {
          &__cost {
            margin-top: 93px;
            margin-left: 59px;
            color: @red;
            font-size: 90px;
            letter-spacing: -0.9px;
            line-height: 64px;
            &__name {
              margin-right: 59px;
              &_up {
                border-color: @red;
              }
            }
          }
          &__size {
            color: @dark;
          }
          &__profit {
            margin-top: 16px;
          }
          &__descript {
            margin-top: 78px;
          }
          &__buy {
            margin-top: 51px;
            padding: 20px 0 19px;
          }
        }
      }
    }
  }
  &__size {
    font-family: FedraSansAltProBold;
    font-size: 30px;
    color: @lightGrey;
    letter-spacing: 0;
    line-height: 22px;
    margin-bottom: 18px;
    &__name {
      font-family: FedraSansAltProMedTF;
      font-size: 14px;
      color: @darkGrey;
      letter-spacing: -0.22px;
      line-height: 18px;
    }
  }
  &__cost {
    font-family: FedraSansAltProMedTF;
    &_xl {
      margin-left: 15px;
    }
    &__name {
      font-family: FedraSansAltProMedTF;
      letter-spacing: 0;
      line-height: 24px;
      width: 40px;
      &_up {
        border-bottom: solid 1px;
        padding-bottom: 5px;
        font-size: 13px;
      }
      &_down {
        font-size: 15px;
      }
    }
  }
  &__profit {
    font-family: FedraSansAltProBookTF;
    font-size: 13px;
    color: @grey;
    letter-spacing: 0.11px;
    line-height: 18px;
  }
  &__descript {
    font-family: FedraSansAltProMedTF;
    font-size: 16px;
    color: @dark;
    letter-spacing: -0.11px;
    line-height: 20px;
    &_pull-down {
      margin-bottom: 19px;
    }
  }
  &__buy {
    border-top: solid 1px @light;
  }
}