.nav {
  &.nav_fix {
    position: fixed;
    left: 0;
    top: 30px;
    height: 115px;
    width: 100%;
    z-index: 1001;
    a {
      color: @dark;
    }
  }
  a{
    color: #fff;
    text-decoration: none;
  }
  &__item {
    width: 241px;
    float: left;
    font-family: FedraSansAltProMedTF;
    font-size: 15px;
    letter-spacing: -0.21px;
    line-height: 20px;
    padding-bottom: 28px;
    a {
      display: block;
    }
    &_active,
    &:hover {
      border-bottom: solid 2px @orange;
    }
    &_small {
      font-family: FedraSansAltProBookTF;
      font-size: 12px;
      letter-spacing: -0.17px;
      line-height: 28px;
      display: block;
    }
  }
}