.header__dropdown {
  margin: -10px auto 0;
  vertical-align: middle;
  width: 200px;
  display: block;
  text-align: center;
  font-size: 13px;
  line-height: 30px;
  .dropdown__content {
    background-color: #fff;
    border-right: 1px;
    color: @darkGrey;
    left: 6px;
    padding-bottom: 7px;
    padding-top: 34px;
    position: absolute;
    top: -4px;
    width: 100%;
    z-index: 1;
    &__item {
      &_selected {
        display: none;
      }
    }
  }
  &__selected {
    color: #fff;
    position: relative;
    z-index: 2;
    margin-left: 12px;
    .sprite;
    &:after {
      position: absolute;
      right: 12px;
      top: 8px;
      .icons;
      .svg-arrowDownWhite;
      .svg-arrowDownWhite-dims;
    }
    &_active {
      color: @red;
      &:after {
        .icons;
        .svg-arrowDownBlack;
        .svg-arrowDownBlack-dims;
      }
    }
  }
}