.tv {
  margin-top: 103px;
  background-color: @lLight;
  position: relative;
  z-index: 4;
  padding-bottom: 29px;
  &__title {
    line-height: 42px;
  }
  &__descript {
    color: @dark;
    width: 400px;
    margin-top: 36px;
    &__title {
      font-family: FedraSansAltProMedTF;
      font-size: 30px;
      line-height: 38px;
      margin-bottom: 27px;
    }
    &__subtitle {
      font-family: FedraSansAltProBookTF;
      font-size: 15px;
      line-height: 26px;
    }
  }
  &__image {
    margin-top: -14px;
    width: 635px;
  }
}
.tv__store {
  margin-top: 38px;
}
.app-store {
  margin-left: 5px;
  display: inline-block;
  background-image: url(../images/appstore.png);
  width: 55px;
  height: 61px;
  background-size: 52px auto;
  background-repeat: no-repeat;
}
.google-play {
  margin-left: 18px;
  display: inline-block;
  background-image: url(../images/googleplay.png);
  width: 71px;
  height: 71px;
  background-size: 71px auto;
  background-repeat: no-repeat;
}