.how {
  margin-top: 112px;
  padding-top: 50px;
  padding-bottom: 92px;
  background-color: @lLight;
  &__title {
    letter-spacing: -0.42px;
    line-height: 68px;
  }
  &__sheme {
    margin-top: 92px;
    margin-left: 50px;
    &__arrow {
      position: absolute;
      right: -6px;
      top: -42px;
      font-size: 30px;
    }
    &__num {
      font-family: FedraSansAltProBold;
      font-size: 14px;
      color: @red;
      letter-spacing: 0;
      line-height: 22px;
    }
    &__descript {
      font-family: FedraSansAltProBookTF;
      font-size: 14px;
      color: @dark;
      letter-spacing: 0;
      line-height: 22px;
    }
    &__step {
      float: left;
      width: 239px;
      position: relative;
      padding-left: 18px;
      border-left: solid 2px @red;
      height: 120px;
      &_dushed {
        &:after {
          border-style: dashed;
        }
      }
      &_solid {
        &:after {
          border-style: solid;
        }
      }
      &:after {
        top: -28px;
        left: 0;
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        border-width: 1px;
        border-bottom: @lightGrey;
        border-color: @lightGrey;
        z-index: 1;
      }
      &:before {
        z-index: 2;
        width: 13px;
        height: 13px;
        background-color: @red;
        border-radius: 50%;
        content: '';
        position: absolute;
        left: -7px;
        top: -35px;
        box-shadow: 0 0 0 4px @lLight, 0 0 0 5px @lightGrey;
        // border: 1px solid @lightGrey;
      }
    }
  }
}