@import '__dropdown/header__dropdown.less';
.header {
  position: fixed;
  width: 100%;
  top: 30px;
  left: 0;
  z-index: 1000;
  -webkit-transition: bottom .5s ease;
  -o-transition: bottom .5s ease;
  transition: bottom .5s ease;
  &_fix {
    background-color: #fff;
    top: 0;
    padding-top: 30px;
    height: 109px;
    border-bottom: solid 1px @light;
    .header__logo .icon {
      font-size: 60px;
    }
    .header__tel__num {
      color: @dark;
    }
    #chooseCity {
      display: none;
    }
  }
  &__logo {
    display: block;
    width: 70px;
    height: 70px;
    .icon {
      font-size: 70px;
    }
  }
  &__tel {
    &:hover {
      .link_red{
        color: saturate(@red, 25%);
      }
    }
    &:active {
      .link_red{
        color: darken(@red, 15%);
      }
    }
    &__num {
      display: block;
      font-family: FedraSansAltProBold;
      font-size: 19px;
      line-height: 23px;
      margin-top: -4px;
      padding-bottom: 12px;
      &_white {
        color: #fff;
      }
    }
    span {
      font-family: FedraSansAltProMedTF;
      letter-spacing: -0.09px;
    }
  }
}