/*RESET-CSS*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  font-family: FedraSansAltProBookTF;
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
*{
  box-sizing: border-box;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix:before, .clearfix:after,
.row:before, .row:after {
  clear: both;
  content: '';
  display: table;
}
.img {
  display: block;
  width: 100%;
  height: auto;
}

/*COLOR*/
@lLight: #F4F4F4;
@light: #E5E5E5;
@lightGrey: #9B9B9B;
@grey: #5C5C5C;
@darkGrey: #4A4A4A;
@dark: #262626;
@red: #F11929;
@orange: #F6C823;
@mOrange: #ECBD13;
@dOrange: #D0AA1D;
/*FONTS*/
.font-face(FedraSansAltProBold, '../../fonts/FedraSansAltProBold/FedraSansAltPro-Bold', 700, normal);
.font-face(FedraSansAltProBookTF, '../../fonts/FedraSansAltProBookTF/FedraSansAltPro-BookTF', 400, normal);
.font-face(FedraSansAltProDemiTF, '../../fonts/FedraSansAltProDemiTF/FedraSansAltPro-DemiTF', 400, normal);
.font-face(FedraSansAltProMedTF, '../../fonts/FedraSansAltProMedTF/FedraSansAltPro-MedTF', 500, normal);

h1,.h1 {
  font-size: 42px;
}
h2,.h2 {
  font-family: FedraSansAltProMedTF;
  font-size: 34px;
  color: @dark;
  letter-spacing: -0.47px;
  line-height: 52px;
}
h3,.h3 {
  font-family: FedraSansAltProMedTF;
  font-size: 30px;
  color: @dark;
  letter-spacing: -0.8px;
}
.subtitle {
  font-family: FedraSansAltProBookTF;
  font-size: 16px;
  color: @darkGrey;
  letter-spacing: 0;
  line-height: 26px;
}
.uppercase {
  text-transform: uppercase;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: center;
}
.d-block {
  display: block;
}
.link {
  text-decoration: none;
  font-size: 13px;
  color: @dark;
  &_red {
    color: @red;
    &:hover {
      color: saturate(@red, 25%);
    }
    &:active {
      color: darken(@red, 15%);
    }
  }
}
.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}
.btn {
  text-align: center;
  border: none;
  font-family: FedraSansAltProMedTF;
  cursor: pointer;
  &_orange {
    background: @orange;
    border-radius: 2px;
    box-shadow: 0 3px 0 @dOrange;
    color: @dark;
    letter-spacing: -0.3px;
    &:hover{
      background: saturate(@orange, 15%);
      box-shadow: 0 3px 0 saturate(@dOrange, 15%);
    }
    &:active {
      background: darken(@orange, 15%);
      box-shadow: 0 3px 0 darken(@dOrange, 15%);
    }
  }
  &_white {
    background: #fff;
    color: @red;
    &:hover {
      background: darken(#fff, 2%);
      color: saturate(@red, 15%);
    }
    &:active {
      background: darken(#fff, 5%);
      color: darken(@red, 15%);
    }
  }
  &_sm {
    font-size: 16px;
    padding: 16px 68px 12px;
    height: 51px;
  }
  &_md {
    font-size: 18px;
    padding: 16px 95px 17px;
  }
  &_xs {
    font-family: FedraSansAltProBold;
    font-size: 18px;
    padding: 24px 42px 22px;
    letter-spacing: -0.34px;
  }
}

/*GRID*/
.container {
  max-width: 1530px;
  width: 100%;
  margin: 0 auto;
  &_lg {
    width: 1280px;
    margin: 0 auto;
  }
  &_md {
    width: 1120px;
    margin: 0 auto;
  }
  &_sm {
    width: 600px;
    margin: 0 auto;
  }
}
.col {
  float: left;
}
.col-4 {
  width: 320px;
  margin: 0 40px;
}
.sprite {
  &:after{
    content: '';
    display: inline-block;
  }
}
